import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ClipboardService } from 'ngx-clipboard';
import { UsersService } from 'src/app/shared/service/users.service';
import { AuthService } from '../../auth/_services/auth.service';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-publisher-documentation',
  templateUrl: './publisher-documentation.component.html',
  styleUrls: ['./publisher-documentation.component.scss']
})
export class PublisherDocumentationComponent implements OnInit {

  currentUser: any;
  tagIds: any = [];
  userApiKey: string;
  selectedCompany: string;
  currentDate: string;
  beforeDate: string;
  constructor(
    private authService: AuthService,
    private userService: UsersService,
    private cdr: ChangeDetectorRef,
    private clipboardService: ClipboardService,
    private datePipe: DatePipe
  ) {
    const today = new Date();
    const previousDate = new Date(today.getTime() - 30 * 24 * 60 * 60 * 1000); // Subtracting 30 day (720 hours)

    this.currentDate = this.datePipe.transform(today, 'MM-dd-yyyy');
    this.beforeDate = this.datePipe.transform(previousDate, 'MM-dd-yyyy');
   }

  ngOnInit(): void {
    this.selectedCompany = this.getSelectedCompanyFromLocalStorage();
    console.log(this.selectedCompany)
    this.currentUser = this.authService.currentUserValue;
    this.userApiKey = this.currentUser.apiKey;
    console.log(this.currentUser)
    if (this.currentUser.tagsId[this.selectedCompany]) {
      this.tagIds = this.currentUser.tagsId[this.selectedCompany].map((tagId: any) => {
        const tagKey = tagId.split("/")[1];
        const tagIdData = {
          tagKey: tagKey,
          copy: false,
        }
        return tagIdData;
      });
    } else {
      this.tagIds = [];
    }
  }

  openClipBoard(apiKey: any, tagId: any) {
    tagId.copy = !tagId.copy;
    const reportApi = `http://api.nextsys.io/api/publisher/reporting/api?apiKey=${apiKey}&tag=${tagId.tagKey}&startDate=${this.beforeDate}&endDate=${this.currentDate}&reportType=csv`
    this.clipboardService.copyFromContent(reportApi);
    setTimeout(() => {
      tagId.copy = !tagId.copy;
      this.cdr.detectChanges();
    }, 3000)
    this.cdr.detectChanges();

  }

  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }

}
